/* Transparent Overlay */
.overlay {
    width: 100vw;
    height: 108vh;
    top: 0;
    position: fixed;    
    background-color: rgba(0, 0, 0, 0.5);
}

/* Modal Box */
.modal {
    top: 20vh;
    left: 30vw;
    right: 0;
    bottom: 0;
    width: 40vw;
    height: 55vh; 
    position: absolute;
    border-radius: 4px;
    border: 1px solid #e0e0e0;      
    background-color: #ffffff;
}

/* Modal Transitions */
.fadeOut {
    opacity: 0;
    -webkit-transition: opacity .5s;
    -o-transition: opacity .5s;
    transition: opacity .5s;
}

.fadeIn {
    opacity: 1;
    -webkit-transition: opacity .5s;
    -o-transition: opacity .5s;
    transition: opacity .5s;
}

@media (max-width: 700px) {
    .modal {
        left: 5vw;
        width: 90vw;
        height: 50vh;                        
    }
}


